@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
    text-decoration: none;
}

a:visited {
    color: #9da6ba;
}

a:hover {
    color: #20abee;
}

a:active {
    color: #9da6ba;
}

.Nav-item {
    font-color: #9da6ba;
    font-family: 'Poppins', sans-serif;
    font-size: 16px;
    word-spacing: 1px;
    font-weight: 100;
    margin: 20px;
    -webkit-font-smoothing: antialiased;
}

.Nav-container {
    position: fixed;
    left: 50%;
    top: 10%;
    display: block;
}

.header {
    font-weight: 500;
    font-size: 48px;
    margin-top: 16px;
    font-family: 'Poppins',sans-serif;
    letter-spacing: 1px;
    position: fixed;
    left: 27%;
    top: 20%; 
}

.content {
    font-weight: 500;
    font-size: 18px;
    line-height: 42px;
    letter-spacing: 1px;
    font-family: 'Poppins',sans-serif;
    position: relative; 
    left: 27%;
    top: 300px; 
}



.head {
    font-family: 'Poppins',sans-serif;
    font-weight: 600;
    font-size: 55px;
    line-height: 40px;
    letter-spacing: 5px;
    position: fixed;
    left: 27%;
    top: 20%; 

}

.subtitle {
    font-family: 'Poppins',sans-serif;
    font-weight: 200;
    font-size: 22px;
    line-height: 40px;
    word-spacing: 1px;
    position: relative;
    left: 27%;
    top: 300px;
    -webkit-font-smoothing: antialiased;
}

.sponsor {
    font-family: 'Poppins',sans-serif;
    font-weight: 200;
    font-size: 22px;
    line-height: 40px;
    word-spacing: 1px;
    position: relative;
    left: 60%;
    top: 300px;
    -webkit-font-smoothing: antialiased;

}

.footer {
    font-family: 'Poppins',sans-serif;
    position: fixed; 
    font-size: 12px;
    font-weight: 300;
    color: #9da6ba;
    display: block;
    position: fixed;
    left: 27%;
    top: 90%;
}

a {
    text-decoration: none;
}

a:visited {
    color: #9da6ba;
}

a:hover {
    color: #20abee;
}

a:active {
    color: #9da6ba;
}


#table {
  font-family: 'Poppins', sans-serif; 
  font-weight: 300;
  font-size: 14px;
  position: fixed;
  top: 25%;
  left: 22%;
  width: 50%;
  text-align: center;
}

.ReactTable .rt-thead {
  overflow-y: scroll;
  position: fixed;
  top: 22%;
  left: 22%;
  width: 50%;
}

.App-header {
  background-color: #f7fbfb;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}


